import React,{Component} from 'react';
import Modal  from 'react-modal';

export default class Ques5 extends Component {
 constructor(props) {
  super(props);
  this.state = {
    showModal1: false, 
    showModal2: false,  
    query:"",
    solutionStatus:"",
    hint1:"",
    hint2:"",
      lock : false,
    hint1_open:false,
  };
}
handleOpenModal1=() => {
  this.setState({ showModal1: true });
}

handleCloseModal1=() => {
  this.setState({ showModal1: false });
}

handleOpenModal2=() => {
  this.setState({ showModal2: true });
}

handleCloseModal2=() => {
 this.setState({ showModal2: false });
}

handle = ()=>{
    if(this.state.lock===false){
  if(this.props.levelChecker()){
			// show the question
			this.handleOpenModal2()
		}else{
			//show not eligible
			this.handleOpenModal1()
		}
	}}

 render(){
  return (
  	<React.Fragment>
  	<img src={require('./l10.png')} onClick={this.handle} class="q5-img"/>
  	<Modal 
   isOpen={this.state.showModal1}
   contentLabel="Minimal Modal Example"
   >
   First Clear Previous Levels!<br/>
   <button onClick={this.handleCloseModal1}>OK</button>
   </Modal>

   <Modal 
   isOpen={this.state.showModal2}
   contentLabel="Minimal Modal Example"
   >
    <center>

   <b>Read the purport </b>of the verse that talks about <br/>
   reproductive part of the plant,<br/>
   energy collector part of plant, <br/>
   Seed bearing part of plant,<br/>
   An essential substance for plants.<br/>
   Only the 
   <input type="text" 
   onChange = {(event) => {
    this.setState({query: event.target.value});
  }}
  />
  can understand that the Supreme Absolute Truth can eat food and enjoy it.
  <br/>
  <button onClick={()=>{
    if(this.state.query==="devotee"){
                  this.setState({
              lock:true,
            })
      this.props.scorePlus(20)
      this.props.nextLevelUnlocker()
      this.handleCloseModal2()
    }
    else{
      this.setState({
        solutionStatus:"Wrong Answer !"
      })
      this.props.scoreMinus(1)
    }
  }}>Submit</button><br/>
  {this.state.solutionStatus}

<br/>
  <button onClick={()=>{
    this.setState({
      hint1:"363",
      hint1_open:true,
    })
    this.props.scoreMinus(5)
  }} >Hint 1</button>
  {this.state.hint1}

  <br/>

  <button onClick={()=>{

    if(this.state.hint1_open === true)
    {
    this.setState({
      hint2:"search chapter 9"
    })
    this.props.scoreMinus(5)
  }else{
        this.setState({
      hint2:"First open hint 1 to know hint 2"
    })
  }



  }} 


  >Hint 2</button>
  {this.state.hint2}

<br/>
<br/>
<br/>

  <button onClick={()=>{
    this.handleCloseModal2()
  }}> Close</button> 
    </center>

  </Modal>
  </ React.Fragment>
  )}
}

