import React,{Component} from 'react';
import Modal  from 'react-modal';

export default class Ques1 extends Component {
	  constructor(props) {
    super(props);
    this.state = {
      showModal1: false, 
      showModal2: false,
      lock : false,
      query:"",
  };
  }
    handleOpenModal1=() => {
    this.setState({ showModal1: true });
  }

  handleCloseModal1=() => {
    this.setState({ showModal1: false });
  }

    handleOpenModal2=() => {
    this.setState({ showModal2: true });
  }

  handleCloseModal2=() => {
    this.setState({ showModal2: false });
  }

	handle = ()=>{
		if(this.props.levelChecker()){
			// show the question
			this.handleOpenModal2()
		}else{
			//show not eligible
			this.handleOpenModal1()
		}
	}

	  render(){
  return (
  	<React.Fragment>
  	<img src={require('./l8.png')} onClick={this.handle} class="q1-img"/>
  	<Modal 
           isOpen={this.state.showModal1}
           contentLabel="Warning"
        >
        First Clear Previous Levels!<br/>
          <button onClick={this.handleCloseModal1}>OK</button>
      </Modal>

      <Modal 
           isOpen={this.state.showModal2}
           contentLabel="Question"
        >
    <center>
        
<img alt="" src={require('./Ques1-first.jpg')} /><br/>
Krsna conscious person has lost all <input type="text" 
        onChange = {(event) => {
        this.setState({query: event.target.value});
        }}
/> for sense gratification. (12 characters)
      
<br/>
<button onClick={()=>{
  if(this.state.query==="inclinations"){
                this.setState({
              lock:true,
            })
                this.props.scorePlus(20)
            this.props.nextLevelUnlocker()
            this.handleCloseModal2()
  }
  else{
                this.setState({
              solutionStatus:"Wrong Answer !"
            })
            this.props.scoreMinus(1)
  }
}}>Submit</button><br/>
{this.state.solutionStatus}


<br/>
<br/>
<br/>
         <button onClick={()=>{
            this.handleCloseModal2()
          }}> Close</button>
    </center>

      </Modal>
  	</ React.Fragment>
  	)}
}

