import React,{Component} from 'react';
import Modal  from 'react-modal';

export default class Ques10 extends Component {
 constructor(props) {
  super(props);
  this.state = {
    showModal1: false, 
    showModal2: false,  
    query:"",
    solutionStatus:"",
    hint1:"",
    hint1_open:false,
    hint2_open:false,
      lock : false,
    hint2:"",
  };
}
handleOpenModal1=() => {
  this.setState({ showModal1: true });
}

handleCloseModal1=() => {
  this.setState({ showModal1: false });
}

handleOpenModal2=() => {
  this.setState({ showModal2: true });
}

handleCloseModal2=() => {
 this.setState({ showModal2: false });
}

handle = ()=>{
    if(this.state.lock===false){
  if(this.props.levelChecker()){
      // show the question
      this.handleOpenModal2()
    }else{
      //show not eligible
      this.handleOpenModal1()
    }
  }}

 render(){
  return (
    <React.Fragment>
    <img src={require('./l1.png')} onClick={this.handle} class="q10-img"/>
    <Modal 
   isOpen={this.state.showModal1}
   contentLabel="Minimal Modal Example"
   >
   First Clear Previous Levels!<br/>
   <button onClick={this.handleCloseModal1}>OK</button>
   </Modal>

   <Modal 
   isOpen={this.state.showModal2}
   contentLabel="Minimal Modal Example"
   >
    <center>

In how many forms the Lord appears in front of Arjun during Bhagwat Gita is spoken?
<br/>
<button onClick={()=>{
        this.setState({
        solutionStatus:"Wrong Answer !"
      })
      this.props.scoreMinus(1)
}}>1</button>

<button onClick={()=>{
        this.setState({
        solutionStatus:"Wrong Answer !"
      })
      this.props.scoreMinus(1)
}}>2</button>

<button onClick={()=>{
              this.setState({
              lock:true,
            })
      this.props.scorePlus(20)
      this.props.nextLevelUnlocker()
      this.handleCloseModal2()
}}>3</button>

<button onClick={()=>{
        this.setState({
        solutionStatus:"Wrong Answer !"
      })
      this.props.scoreMinus(1)
}}>4</button>

  {this.state.solutionStatus}

<br/>
  <button onClick={()=>{
    this.setState({
      hint1:"The Universal form",
      hint1_open:true,

    })
    this.props.scoreMinus(5)
  }} >Hint 1</button>
  {this.state.hint1}

  <br/>

   <button onClick={()=>{

    if(this.state.hint1_open === true)
    {
    this.setState({
      hint2:"Check chapter 11",
      hint2_open:true,
   
    })
    this.props.scoreMinus(5)
  }else{
        this.setState({
      hint2:"First open hint 1 to know hint 2"
    })
  }
  }} 
  >Hint 2</button>
  {this.state.hint2}



  <button onClick={()=>{

    if(this.state.hint2_open === true)
    {
    this.setState({
      hint3:"Four Handed form"
    })
    this.props.scoreMinus(5)
  }else{
        this.setState({
      hint3:"First open hint 2 to know hint 3"
    })
  }



  }} 


  >Hint 3</button>
  {this.state.hint3}


<br/>
<br/>
<br/>

  <button onClick={()=>{
    this.handleCloseModal2()
  }}> Close</button> 
    </center>

  </Modal>
  </ React.Fragment>
  )}
}

