import React,{Component} from 'react';
import Modal  from 'react-modal';

export default class Ques9 extends Component {
 constructor(props) {
  super(props);
  this.state = {
    showModal1: false, 
    showModal2: false,  
    query:"",
    solutionStatus:"",
    hint1:"",
    hint1_open:false,
      lock : false,
    hint2:"",
  };
}
handleOpenModal1=() => {
  this.setState({ showModal1: true });
}

handleCloseModal1=() => {
  this.setState({ showModal1: false });
}

handleOpenModal2=() => {
  this.setState({ showModal2: true });
}

handleCloseModal2=() => {
 this.setState({ showModal2: false });
}

handle = ()=>{
    if(this.state.lock===false){
  if(this.props.levelChecker()){
      // show the question
      this.handleOpenModal2()
    }else{
      //show not eligible
      this.handleOpenModal1()
    }
  }}

 render(){
  return (
    <React.Fragment>
    <img src={require('./l9.png')} onClick={this.handle} class="q9-img"/>
    <Modal 
   isOpen={this.state.showModal1}
   contentLabel="Minimal Modal Example"
   >
   First Clear Previous Levels!<br/>
   <button onClick={this.handleCloseModal1}>OK</button>
   </Modal>

   <Modal 
   isOpen={this.state.showModal2}
   contentLabel="Minimal Modal Example"
   >
    <center>

I think you have played to much for today. You should stop. YOU SHOULD STOP EVERYTHING. You should stop your all activities, eating, sleeping everything, no need to do any thing. Only then you’ll we able to worship God. <br/>
Where does lord refutes above statement? The last word of the translation will be key for the next verse.

<br/>

   <input type="text" 
   onChange = {(event) => {
    this.setState({query: event.target.value});
  }}
  />

  <br/>
  <button onClick={()=>{
    if(this.state.query==="doubt"){
                  this.setState({
              lock:true,
            })
      this.props.scorePlus(20)
      this.props.nextLevelUnlocker()
      this.handleCloseModal2()
    }
    else{
      this.setState({
        solutionStatus:"Wrong Answer !"
      })
      this.props.scoreMinus(1)
    }
  }}>Submit</button><br/>
  {this.state.solutionStatus}

<br/>
  <button onClick={()=>{
    this.setState({
      hint1:"chapter 8",
      hint1_open:true,
      
    })
    this.props.scoreMinus(5)
  }} >Hint 1</button>
  {this.state.hint1}

  <br/>

   <button onClick={()=>{

    if(this.state.hint1_open === true)
    {
    this.setState({
      hint2:"verse number is less than chapter number"
    })
    this.props.scoreMinus(5)
  }else{
        this.setState({
      hint2:"First open hint 1 to know hint 2"
    })
  }



  }} 


  >Hint 2</button>
  {this.state.hint2}



<br/>
<br/>
<br/>
  <button onClick={()=>{
    this.handleCloseModal2()
  }}> Close</button> 
    </center>

  </Modal>
  </ React.Fragment>
  )}
}

