import React,{Component} from 'react';
import Modal  from 'react-modal';

export default class Ques2 extends Component {
 constructor(props) {
  super(props);
  this.state = {
    showModal1: false, 
    showModal2: false ,
    query:"",
    solutionStatus:"",
      lock : false,

  };
}
handleOpenModal1=() => {
  this.setState({ showModal1: true });
}

handleCloseModal1=() => {
  this.setState({ showModal1: false });
}

handleOpenModal2=() => {
  this.setState({ showModal2: true });
}

handleCloseModal2=() => {
 this.setState({ showModal2: false });
}

handle = ()=>{
    if(this.state.lock===false){

  if(this.props.levelChecker()){
			// show the question
			this.handleOpenModal2()
		}else{
			//show not eligible
			this.handleOpenModal1()
		}
	}
}
 render(){
  return (
  	<React.Fragment>
  	<img src={require('./l6.png')} onClick={this.handle} class="q2-img"/>
  	<Modal 
   isOpen={this.state.showModal1}
   contentLabel="Warning"
   >
   First Clear Previous Levels!<br/>
   <button onClick={this.handleCloseModal1}>OK</button>
   </Modal>

   <Modal 
   isOpen={this.state.showModal2}
   contentLabel="Question"
   >
    <center>
   
   Hey! I found a Sanskrit line containing the key to supreme mind control! <br/>
   But I have only a portion of it  - “asamsayam maha baho..”. But how can I find  it in gita!! <br/>
   I found an author telling us the location from where we can find out more about the verse
   Location - Index a Sanscritica Versus<br/>
   But it is in Latin!! And I don’t know it, do you know anyone who knows Latin?<br/>
   Can you help me find out the secret?<br/>
   <br/>



   By discharge of devotional service one feels transcendental <input type="text" 
   onChange = {(event) => {
    this.setState({query: event.target.value});
  }}
  /> (12 words)
  <br/>
  <button onClick={()=>{
    if(this.state.query==="satisfaction"){
                  this.setState({
              lock:true,
            })
      this.props.scorePlus(20)
      this.props.nextLevelUnlocker()
      this.handleCloseModal2()
    }
    else{
      this.setState({
        solutionStatus:"Wrong Answer !"
      })
      this.props.scoreMinus(1)
    }
  }}>Submit</button><br/>
  {this.state.solutionStatus}

  
<br/>
<br/>
<br/>
  <button onClick={()=>{
    this.handleCloseModal2()
  }}> Close</button>
    </center>

  </Modal>
  </ React.Fragment>
  )}
}

