import React,{Component} from 'react';
import Modal  from 'react-modal';

export default class Ques4 extends Component {
	  constructor(props) {
    super(props);
    this.state = {
      showModal1: false, 
      showModal2: false,
            query:"",
    solutionStatus:"",
      lock : false,

  };
  }
    handleOpenModal1=() => {
    this.setState({ showModal1: true });
  }

  handleCloseModal1=() => {
    this.setState({ showModal1: false });
  }

    handleOpenModal2=() => {
    this.setState({ showModal2: true });
  }

  handleCloseModal2=() => {
    this.setState({ showModal2: false });
  }

	handle = ()=>{
    if(this.state.lock===false){
		if(this.props.levelChecker()){
			// show the question
			this.handleOpenModal2()
		}else{
			//show not eligible
			this.handleOpenModal1()
		}
	}}

	  render(){
  return (
  	<React.Fragment>
  	<img src={require('./l4.png')} onClick={this.handle} class="q4-img"/>
  	<Modal 
           isOpen={this.state.showModal1}
           contentLabel="erroe"
        >

        First Clear Previous Levels!<br/>
          <button onClick={this.handleCloseModal1}>OK</button>
      </Modal>

      <Modal 
           isOpen={this.state.showModal2}
           contentLabel="question"
        >
    <center>

Finally, answer is in purport itself. <br/>
<img src={require('./Ques4-first.jpg')} alt="" /><br/>
When one is in mode of passion one is mentally <input type="text" 
        onChange = {(event) => {
        this.setState({query: event.target.value});
        }}
/>
<br/>
  <button onClick={()=>{
    if(this.state.query==="unhappy"){
                  this.setState({
              lock:true,
            })
      this.props.scorePlus(20)
      this.props.nextLevelUnlocker()
      this.handleCloseModal2()
    }
    else{
      this.setState({
        solutionStatus:"Wrong Answer !"
      })
      this.props.scoreMinus(1)
    }
  }}>Submit</button><br/>
  {this.state.solutionStatus}


<br/>
<br/>
<br/>
  
  <button onClick={()=>{
    this.handleCloseModal2()
  }}> Close</button> 
    </center>

      </Modal>
  	</ React.Fragment>
  	)}
}

