import React,{Component} from 'react';
import Modal  from 'react-modal';

export default class Ques7 extends Component {
 constructor(props) {
  super(props);
  this.state = {
    showModal1: false, 
    showModal2: false,  
    query:"",
    solutionStatus:"",
    hint1:"",
    hint1_open:false,
      lock : false,
    hint2:"",
  };
}
handleOpenModal1=() => {
  this.setState({ showModal1: true });
}

handleCloseModal1=() => {
  this.setState({ showModal1: false });
}

handleOpenModal2=() => {
  this.setState({ showModal2: true });
}

handleCloseModal2=() => {
 this.setState({ showModal2: false });
}

handle = ()=>{
    if(this.state.lock===false){
  if(this.props.levelChecker()){
      // show the question
      this.handleOpenModal2()
    }else{
      //show not eligible
      this.handleOpenModal1()
    }
  }}

 render(){
  return (
    <React.Fragment>
    <img src={require('./l7.png')} onClick={this.handle} class="q7-img"/>
    <Modal 
   isOpen={this.state.showModal1}
   contentLabel="Minimal Modal Example"
   >
   First Clear Previous Levels!<br/>
   <button onClick={this.handleCloseModal1}>OK</button>
   </Modal>

   <Modal 
   isOpen={this.state.showModal2}
   contentLabel="Minimal Modal Example"
   >
    <center>

Out of many thousands one may dare to go on the path of perfection. Among them only few know the path to perfection. <br/>
Find the verse related to the above statement and read the verse present in the last paragraph of the purport. This verse number is you key to next answer. <br/>
(Note : A.A.ABC should be written as AAABC)<br/>
<br/>

   <input type="text" 
   onChange = {(event) => {
    this.setState({query: event.target.value});
  }}
  />

  <br/>
  <button onClick={()=>{
    if(this.state.query==="12234"){
                  this.setState({
              lock:true,
            })
      this.props.scorePlus(20)
      this.props.nextLevelUnlocker()
      this.handleCloseModal2()
    }
    else{
      this.setState({
        solutionStatus:"Wrong Answer !"
      })
      this.props.scoreMinus(1)
    }
  }}>Submit</button><br/>
  {this.state.solutionStatus}

<br/>
  <button onClick={()=>{
    this.setState({
      hint1:"Find where lord talks about knowledge of the absolute",
      hint1_open:true,

    })
    this.props.scoreMinus(5)
  }} >Hint 1</button>
  {this.state.hint1}

  <br/>

   <button onClick={()=>{

    if(this.state.hint1_open === true)
    {
    this.setState({
      hint2:"Bhagwat Gita can be seen as divided into 3 parts based on its content. This  chapter is the first of the middle of Gita "
    })
    this.props.scoreMinus(5)
  }else{
        this.setState({
      hint2:"First open hint 1 to know hint 2"
    })
  }



  }} 


  >Hint 2</button>
  {this.state.hint2}


<br/>
<br/>
<br/>

  <button onClick={()=>{
    this.handleCloseModal2()
  }}> Close</button> 
    </center>

  </Modal>
  </ React.Fragment>
  )}
}

