import React,{Component} from 'react';
import Ques0 from "./Questions/Ques0"
import Ques1 from "./Questions/Ques1"
import Ques2 from "./Questions/Ques2"
import Ques3 from "./Questions/Ques3"
import Ques4 from "./Questions/Ques4"
import Ques5 from "./Questions/Ques5"
import Ques6 from "./Questions/Ques6"
import Ques7 from "./Questions/Ques7"
import Ques8 from "./Questions/Ques8"
import Ques9 from "./Questions/Ques9"
import Ques10 from "./Questions/Ques10"
import WebFont from 'webfontloader';
import ReactPlayer from 'react-player/youtube'
import './App.css';
import './Bg.css';

WebFont.load({
  google: {
    families: ['Special Elite']
  }
});


class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      level : [true, false, false, false, false, false, false, false, false, false, false, false, false, false, false, false, false],
      // level : [true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true, true],
      score : 0,
      youtube_url:"Qmtw4aFpT_U",
    }
  }
  changeUrl=(rr)=>{
    this.setState({
      youtube_url: rr,
    })
  }
  unlockNext(key){
    // this.state.level[key+1] = true;
    var temp = this.state.level.slice(0,key+1);
    temp = temp.concat([true]);
    temp = temp.concat(this.state.level.slice(key+2,this.state.level.length));
    this.setState({
      level :  temp,
    })
  }

  checkLevel(key){
    // if(this.state.level[key]) {
    //   return true;
    // } else{
    //   return false;
    // }
  return this.state.level[key];
  }

  scorePlus = (incr)=>{
    this.setState({
      score : this.state.score + incr,
    })
  }

  scoreMinus = (incr)=>{
    this.setState({
      score : this.state.score - incr,
    })
  }
    // <audio autoPlay loop><source src={require('./relax.wav')} type="audio/wav" />ferfer</audio>

  render(){
  return (
    <div className="App">
    <div className="score">Score : {this.state.score}</div>
    <Ques0 className="l0" levelChecker={() => {return this.checkLevel(0)}} nextLevelUnlocker={()=>{return this.unlockNext(0)}} scorePlus={this.scorePlus} scoreMinus={this.scoreMinus}/>
    <Ques10 className="l1" levelChecker={() => {return this.checkLevel(1)}} nextLevelUnlocker={()=>{return this.unlockNext(1)}} scoreMinus={this.scoreMinus} scorePlus={this.scorePlus}/>
    <Ques8 className="l2" levelChecker={() => {return this.checkLevel(2)}} nextLevelUnlocker={()=>{return this.unlockNext(2)}} scoreMinus={this.scoreMinus} scorePlus={this.scorePlus}/>
    <Ques3 className="l3" levelChecker={() => {return this.checkLevel(3)}} nextLevelUnlocker={()=>{return this.unlockNext(3)}} scoreMinus={this.scoreMinus} scorePlus={this.scorePlus}/>
    <Ques4 className="l4" levelChecker={() => {return this.checkLevel(4)}} nextLevelUnlocker={()=>{return this.unlockNext(4)}} scoreMinus={this.scoreMinus} scorePlus={this.scorePlus}/>
    <Ques6 className="l5" levelChecker={() => {return this.checkLevel(5)}} nextLevelUnlocker={()=>{return this.unlockNext(5)}} scoreMinus={this.scoreMinus} scorePlus={this.scorePlus}/>
    <Ques2 className="l6" levelChecker={() => {return this.checkLevel(6)}} nextLevelUnlocker={()=>{return this.unlockNext(6)}} scoreMinus={this.scoreMinus} scorePlus={this.scorePlus}/>
    <Ques7 className="l7" levelChecker={() => {return this.checkLevel(7)}} nextLevelUnlocker={()=>{return this.unlockNext(7)}} scoreMinus={this.scoreMinus} scorePlus={this.scorePlus}/>
    <Ques1 className="l8" levelChecker={() => {return this.checkLevel(8)}} nextLevelUnlocker={()=>{return this.unlockNext(8)}} scoreMinus={this.scoreMinus} scorePlus={this.scorePlus}/>
    <Ques9 className="l9" levelChecker={() => {return this.checkLevel(9)}} nextLevelUnlocker={()=>{return this.unlockNext(9)}} scoreMinus={this.scoreMinus} scorePlus={this.scorePlus}/>
    <Ques5 className="l10" levelChecker={() => {return this.checkLevel(10)}} nextLevelUnlocker={()=>{return this.unlockNext(10)}} scoreMinus={this.scoreMinus} scorePlus={this.scorePlus}/>
    <img src={require("./map.png")} className="bg"/>

    </div>
  );
  }
}

export default App;